@media (min-width: 1400px) {
    .container, .container-xl {
        max-width: 1020px;
    }
}


body {
    background: #fff000;
}

.hero-wrap {
    height: 100vh;
    min-height: 100%;
    position: relative;
    background-image: url(/static/media/code.52766a4b.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.container {
    width: 100%;
    padding-right: .75rem;
    padding-right: var(--bs-gutter-x, .75rem);
    padding-left: .75rem;
    padding-left: var(--bs-gutter-x, .75rem);
    margin-right: auto;
    margin-left: auto;
}

.slider-text {
    height: 100vh;
    min-height: 750px;
    z-index: 3;
}



.col-lg {
    flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    padding-right: calc(var(--bs-gutter-x) / 2);
    padding-left: calc(var(--bs-gutter-x) / 2);
    margin-top: var(--bs-gutter-y);
    background-color: rgb(0,0,0,.5);
    padding: 10px;
}

.row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(var(--bs-gutter-y) * -1);
    margin-right: calc(var(--bs-gutter-x) / -2);
    margin-left: calc(var(--bs-gutter-x) / -2);
    
}

.mb-4 {
    margin-bottom: 1.5rem!important;
}

.mb-4-p {
    color:rgb(250, 250, 250, 0.9);
    font-family: 'B612 Mono', monospace;
}

.mt-5 {
    margin-top: 5px;
}

.box {
    letter-spacing: 1px;
    margin-bottom: 15px;
    margin-left: 5px;
    z-index: 0;
    display: inline-block;
    border-radius: 4px;
    padding: 4px 9px;
    background: white;
    text-align: center;
    font-family: 'B612 Mono', monospace;
    font-weight: 800;
    font-size: 20px;
    color: black;
    font-family: sans-serif;
    position: relative;
}

.sb:before {
    content:"";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid white;
    border-bottom: 10px solid transparent;
    left: 19px;
    bottom: -19px;

}

.container h1 {
    color: white;
    font-family: 'B612 Mono', monospace;
}

.Typewriter {
    color: white;
    font-family: 'B612 Mono', monospace;
    font-size: 40px;
    
}

.Typewriter__cursor {
    color: orange;
}

.social-media {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 13px;
    letter-spacing: 1px;
}

.social-media a{
    color:white;
    margin-right: 15px;
    
}

h2 {
    font-size: 40px;
    font-weight: 700;
    line-height: 1.4;
}

p {
    font-family: 'Roboto', sans-serif;
}

h3 {
    font-size: 24px;
    font-weight: 700;
}

.mono {
    font-family: 'B612 Mono', monospace;
}
.ftco-section {
    padding: 8em 0;
    position: relative;
    width: 100%;
    overflow-x: hidden;
}
.container {
    width: 100%;
    padding-right: .75rem;
    padding-right: var(--bs-gutter-x, .75rem);
    padding-left: .75rem;
    padding-left: var(--bs-gutter-x, .75rem);
    margin-right: auto;
    margin-left: auto;
}

.img-about{
    background-image: url(/static/media/mountain_man.997ee958.jpeg);
    height: 600px;
    position: relative;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.img-about:before{
    position: absolute;
    bottom: 130px;
    left: -30px;
    width: 100%;
    height: 100px;
    content: '';
    background: #5de4ff;
    z-index: -1;
}

.img-about:after{
    position: absolute;
    bottom: 30px;
    left: -30px;
    width: 100%;
    height: 100px;
    content: '';
    background: rgb(224, 0, 224);
    z-index: -1;
}

.my-5 {
    flex-wrap: nowrap;
}

.nav-item {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
}

.nav-item .nav-link {
